import PropTypes from "prop-types"
import React from "react"

const sectionStyles = {
  padding: `5rem 2rem`,
  margin: `0 auto`,
  maxWidth: `1400px`
};

const subSectionContainer = {
  display: `flex`,
  justifyContent: `space-evenly`,
  flexWrap: `wrap`,
}

const aboutContainerStyles = {
  textAlign: `center`,
  width: `260px`,
  margin: `0px 90px 75px 90px`,
  minHeight: `265px`
}

const AboutSection = ({ hobbies }) => (
  <section id="about" style={sectionStyles}>
    <h1 className="section-header">About Me</h1>
    <div style={subSectionContainer}>
      {hobbies.map((hobby, index) => (
        <div className="hobby-container" key={index} style={aboutContainerStyles}>
          <div className="hobby-icon">
            {hobby.icon}
          </div>
          <div className="hobby-description">
            <strong>{hobby.title}: </strong>
            {hobby.description}
          </div>
        </div>
      ))}
    </div>
  </section>
)

AboutSection.propTypes = {
  hobbies: PropTypes.arrayOf(PropTypes.shape({
    icon: PropTypes.string,
    description: PropTypes.string,
    title: PropTypes.string
  })),
}

AboutSection.defaultProps = {
  hobbies: {},
}

export default AboutSection
